<template>
  <view class="login-container">
    <view class="header">
      <image class="logo" src="/static/logo.png" mode="aspectFit"></image>
      <text class="welcome-text">Ê¨¢ËøéÊù•Âà∞ÈÖíÂ∫óÈ¢ÑËÆ¢</text>
      <text class="sub-text">ËØ∑ËæìÂÖ•ÊÇ®ÁöÑË¥¶Âè∑ÂØÜÁ†ÅËøõË°åÁôªÂΩï</text>
    </view>

    <view class="form-group">
      <view class="input-item">
        <text class="icon-text">üë§</text>
        <input 
          class="input" 
          type="text" 
          v-model="loginForm.username" 
          placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç" 
          placeholder-style="color: #c0c4cc;"
        />
      </view>

      <view class="input-item">
        <text class="icon-text">üîí</text>
        <input 
          class="input" 
          :password="!showPassword" 
          v-model="loginForm.password" 
          placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å" 
          placeholder-style="color: #c0c4cc;"
        />
        <view class="eye-btn" @click="togglePassword">
          <text v-if="showPassword">üëÅÔ∏è</text>
          <text v-else>üôà</text>
        </view>
      </view>

      <view class="input-item">
        <text class="icon-text">üõ°Ô∏è</text>
        <input 
          class="input" 
          type="text" 
          v-model="loginForm.code" 
          placeholder="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å" 
          placeholder-style="color: #c0c4cc;"
          maxlength="4"
        />
        <view class="captcha-box" @click="getCodeImage">
          <image v-if="captchaImg" :src="captchaImg" class="captcha-img" mode="aspectFit"></image>
          <text v-else style="font-size: 24rpx; color: #999;">Âä†ËΩΩ‰∏≠...</text>
        </view>
      </view>
    </view>

    <view class="agreement-box" @click="toggleAgree">
      <view class="checkbox" :class="{ 'checked': isAgree }">
        <text v-if="isAgree" class="check-icon">‚úì</text>
      </view>
      <text class="agree-text">ÊàëÂ∑≤ÈòÖËØªÂπ∂ÂêåÊÑè</text>
      <text class="link" @click.stop="goProtocol">„ÄäÁî®Êà∑ÊúçÂä°ÂçèËÆÆ„Äã</text>
    </view>

    <button 
      class="login-btn" 
      :class="{ 'btn-active': isReadyToLogin }" 
      @click="handleLogin"
    >
      Á´ãÂç≥ÁôªÂΩï
    </button>

    <view class="bottom-links">
      <text class="link-text" @click="goRegister">Ê≤°ÊúâË¥¶Âè∑ÔºüÁÇπÂáªÊ≥®ÂÜå</text>
    </view>
  </view>
</template>

<script>
import http from '@/utils/request.js'

export default {
  data() {
    return {
      loginForm: {
        username: '',
        password: '',
        code: '',     
        uuid: ''      
      },
      captchaImg: '', 
      isAgree: false, 
      showPassword: false // Êñ∞Â¢ûÔºöÊéßÂà∂ÂØÜÁ†ÅÊòØÂê¶ÊòæÁ§∫ÁöÑÂºÄÂÖ≥
    };
  },
  computed: {
    // Ë¥¶Âè∑„ÄÅÂØÜÁ†Å„ÄÅÈ™åËØÅÁ†ÅÂ°´‰∫ÜÔºå‰∏îÂãæÈÄâ‰∫ÜÂçèËÆÆÔºåÊåâÈíÆÊâç‰∫ÆËµ∑
    isReadyToLogin() {
      return this.loginForm.username.length > 0 && 
             this.loginForm.password.length > 0 && 
             this.loginForm.code.length > 0 && 
             this.isAgree;
    }
  },
  onLoad() {
    // È°µÈù¢‰∏ÄÂä†ËΩΩÔºåÂ∞±ÂéªÂêëÂêéÁ´ØËØ∑Ê±ÇÈ™åËØÅÁ†ÅÂõæÁâá
    this.getCodeImage();
  },
  methods: {
    // ÂàáÊç¢ÂØÜÁ†ÅÊòæÁ§∫Áä∂ÊÄÅ
    togglePassword() {
      this.showPassword = !this.showPassword;
    },

    // ÂàáÊç¢ÂçèËÆÆÂãæÈÄâÁä∂ÊÄÅ
    toggleAgree() {
      this.isAgree = !this.isAgree;
    },
    
    // Ëé∑ÂèñÂõæÂΩ¢È™åËØÅÁ†Å
    getCodeImage() {
      http.get('/captchaImage').then(res => {
        if (res.code === 200) {
          this.captchaImg = 'data:image/gif;base64,' + res.img;
          this.loginForm.uuid = res.uuid;
        } else {
          uni.showToast({ title: 'Ëé∑ÂèñÈ™åËØÅÁ†ÅÂ§±Ë¥•', icon: 'none' });
        }
      }).catch(err => {
        console.error('È™åËØÅÁ†ÅËØ∑Ê±ÇÂ§±Ë¥•', err);
      });
    },

    // Êèê‰∫§ÁôªÂΩï
    handleLogin() {
      if (!this.isAgree) {
        uni.showToast({ title: 'ËØ∑ÂÖàÈòÖËØªÂπ∂ÂãæÈÄâÂ∫ïÈÉ®ÂçèËÆÆ', icon: 'none' });
        return;
      }
      if (!this.loginForm.username || !this.loginForm.password) {
        uni.showToast({ title: 'Ë¥¶Âè∑ÂíåÂØÜÁ†Å‰∏çËÉΩ‰∏∫Á©∫', icon: 'none' });
        return;
      }
      if (!this.loginForm.code) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•È™åËØÅÁ†Å', icon: 'none' });
        return;
      }
    
      uni.showLoading({ title: 'ÁôªÂΩï‰∏≠...' });
      
      // 1. ÂèëËµ∑ÁôªÂΩïËØ∑Ê±Ç
      http.post('/login', this.loginForm).then(res => {
        if (res.code === 200 && res.token) {
          // 2. Â≠òÂÇ® Token
          uni.setStorageSync('token', res.token);
          
          // ‚ú® ÂÖ≥ÈîÆÊ≠•È™§ÔºöÁôªÂΩïÊàêÂäüÂêéÁ´ãÂç≥ËØ∑Ê±ÇËé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
          this.getUserDetail(); 
    
        } else {
          uni.hideLoading();
          uni.showToast({ title: res.msg || 'ÁôªÂΩïÂ§±Ë¥•', icon: 'none' });
          this.getCodeImage();
          this.loginForm.code = ''; 
        }
      }).catch(err => {
        uni.hideLoading();
        uni.showToast({ title: err.msg || 'ÊúçÂä°Âô®ÂºÇÂ∏∏', icon: 'none' });
        this.getCodeImage(); 
      });
    },
    
    // ‚ú® Êñ∞Â¢ûÊñπÊ≥ïÔºöËé∑ÂèñÂπ∂Â≠òÂÇ®Áî®Êà∑‰ø°ÊÅØ
    getUserDetail() {
      http.get('/getInfo').then(res => {
        uni.hideLoading();
        if (res.code === 200) {
          // 1. Â≠òÂÇ®ÁúüÂÆûÁöÑ userIdÔºå‰æõ‰∏ãÂçïÈ°µÈù¢ create.vue ‰ΩøÁî®
          uni.setStorageSync('userId', res.user.userId);
          // 2. (ÂèØÈÄâ) Â≠òÂÇ®Êï¥‰∏™Áî®Êà∑‰ø°ÊÅØÊàñÁî®Êà∑Âêç
          uni.setStorageSync('userInfo', res.user);
          
          uni.showToast({ title: 'ÁôªÂΩïÊàêÂäü', icon: 'success' });
          
          // 3. Ë∑≥ËΩ¨È°µÈù¢
          setTimeout(() => {
            uni.navigateBack({
              fail: () => { uni.switchTab({ url: '/pages/index/index' }); }
            });
          }, 1000);
        } else {
          uni.showToast({ title: 'Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•', icon: 'none' });
        }
      }).catch(err => {
        uni.hideLoading();
        console.error('Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂºÇÂ∏∏', err);
      });
    },

    goProtocol() {
      uni.showToast({ title: 'ÂçèËÆÆÂä†ËΩΩ‰∏≠...', icon: 'none' });
    },
    
    // Ë∑≥ËΩ¨Ê≥®ÂÜåÈ°µ
    goRegister() {
      uni.navigateTo({
        url: '/pages/register/register'
      });
    }
  }
};
</script>

